* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* arrow class  */
.arrow {
  border: solid rgb(248, 247, 247);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

/* arrow class  */

.Header {
  display: flex;
  flex-direction: column;
  width: 98%;
  height: 80px;
  /* background-color: rgb(40, 6, 83); */
  background-color: rgb(46, 11, 92);
  color: white;

  font-family: "Mulish", sans-serif;
  position: relative;
}
.top__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  color: #fff;
  margin-bottom: 15px;
  position: relative;
}

.brand__name {
  color: #fff;
  text-decoration: none;
  margin-left: 80px;
  margin-right: 640px;
  font-size: 13px;
}
.menu__itemlist {
  right: 150px;
}
.menu__item {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
  padding-bottom: 21px;
  font-size: 13px;
}
.header__profile {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 85px;
}
.user__profile {
  width: 35px;
  height: auto;
  border: 2px solid #fff;
  border-radius: 5px;
  margin-right: 5px;
}
.user__name {
  margin-right: 10px;
  font-family: "Mulish", sans-serif;
  cursor: pointer;
  font-size: 13px;
}
.user__dropdown {
  cursor: pointer;
}
.line__div {
  position: absolute;
  height: 1px;
  width: 100%;
  top: 70px;
  color: rgb(245, 234, 234);
  border-bottom: 1px solid rgb(56 42 74);
  box-shadow: 1px 1px -1px #26113a;
}
.menuitem__active {
  border-bottom: 3px solid rgb(10, 250, 10);
}

.Header__user {
  display: flex;
  flex-direction: column;
  width: 98%;
  height: 100px;
  /* background-color: rgb(40, 6, 83); */
  background-color: #2e0b5c;
  color: white;
  border-bottom-right-radius: 35px;
  font-family: "Mulish", sans-serif;
  position: relative;
}

.down__header {
  margin-top: 20px;
}
.down__header__line1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 5px;
}
.line1__leftside {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 75px;
}
.line1__leftside__icon {
  font-size: 30px !important;
}
.line1__leftside__text {
  font-size: 20px;
}
.line1__rightside {
  margin-right: 75px;
}
.line1__rightside button:nth-child(1) {
  width: 120px;
  height: 35px;
  padding: 10px 10px;
  border: none;
  outline: none;
  font-size: 12px;
  background-color: rgb(7, 111, 248);
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
}
.line1__rightside button:nth-child(2) {
  width: 80px;
  height: 35px;
  padding: 10px 10px;
  border: none;
  outline: none;
  font-size: 12px;
  background-color: rgb(49, 36, 66);
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
}
.down__header__line2 {
  width: 88%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}
.alphabet__child {
  font-size: 11px;
  color: white;
  text-decoration: none;
  position: relative;
}
.down__header__line__itemactive::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  top: 130%;
  background-color: #fff;
  border-radius: 50%;
  left: -100%;
  overflow: hidden;
}

/* intermediate nav bar */
.intermediate__nav__bar {
  margin-top: 20px !important;
  width: 88%;
  margin: auto;
  display: flex;
  align-items: center;
  position: relative;
}
.employ__status__side {
  width: 30%;
}
.employ__status__side button:nth-child(1) {
  width: 120px;
  height: 35px;
  padding: 10px 10px;
  border: none;
  outline: none;
  font-size: 12px;
  background-color: rgb(7, 111, 248);
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
}
.employ__status__side button:nth-child(2) {
  width: 120px;
  height: 35px;
  padding: 10px 10px;
  border: none;
  outline: none;
  font-size: 12px;
  background-color: rgb(235, 233, 233);
  color: #000;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
}
.employ__list__side {
  display: flex;
  width: 70%;
}
.search__section {
  width: 500px;
  margin-right: 55px;
  position: relative;
}
.search__section input {
  width: 100%;
  padding: 8px 5px;
  background-color: rgb(235, 233, 233);
  border: none;
  outline: none;
  border-radius: 5px;
  color: rgb(66, 65, 65);
}
.search__section__icon {
  position: absolute;
  right: 5px;
  top: 15%;
  color: rgb(66, 65, 65);
  cursor: pointer;
}
.option__section {
  display: flex;
  justify-content: space-between;
  width: 30%;
  padding: 8px 0;
}
.option__section__item {
  text-decoration: none;
  color: #000;
  font-family: "Heebo", sans-serif;
  font-size: 13px;
}
.line__2 {
  position: absolute;
  height: 1px;
  width: 100%;
  top: 45px;
  color: rgb(245, 234, 234);
  border-bottom: 1px solid rgba(122, 118, 118, 0.3);
}
/***********************************/

/* user body */

.user__body {
  width: 88%;
  margin: auto;
  display: flex;
  margin-top: 20px;
}
.employ__status__section {
  width: 30%;
  padding-right: 50px;
}
.employ__status__section__nav {
  display: flex;
  justify-content: space-between;
}
.employ__status__section__nav__heading {
  font-size: 20px;
  font-style: bold;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
}
.three__dot__icon {
  cursor: pointer;
}

.employ__status__section__rate {
  margin: 25px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.rate__card {
  width: 273px;
  height: 150px;
  /* border: 2px solid red; */
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: transparent;
  /* background-color: rgba(14, 218, 14, 0.39); */
  background-color: #a4e0d6cc;
  border-radius: 15px;
}
.rate__card__innerbox {
  width: 110px;
  height: 110px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3px;
}
.rate__percentage {
  font-size: 35px;
  font-family: "Sarabun", sans-serif;
  /* border-bottom: 4px solid green; */
  padding: 32px 18px;
  position: relative;
  color: #0efd7a;
}
.rate__percentage::before {
  content: "";
  position: absolute;
  top: 98%;
  left: 0;
  width: 68%;
  height: 4px;
  background-color: #0efd7a;
  /* border: 2px solid green; */
}
.rate__percentage::after {
  content: "";
  position: absolute;
  top: 78%;
  left: 0;
  width: 68%;
  height: 22px;
  background: linear-gradient(to right, #fff, #a4e0d6);
  /* border: 2px solid green; */
}
.rate__card__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.rate__card__info div:nth-child(1) {
  font-family: "Sarabun", sans-serif;
  color: #000;
}
.rate__card__info div:nth-child(2) {
  font-family: "Archivo", sans-serif;
  color: #000;
  font-size: 20px;
}
.rate__card__info div:nth-child(3) {
  color: #688681;
  font-size: 12px;
  margin-top: 25px;
}
.rate__card__info div:nth-child(4) {
  font-family: "Archivo", sans-serif;
  font-size: 15px;
  color: #11ec2e;
}
.essv__outer__data {
  margin-left: 25px;
}
.essv__outer__data div:nth-child(1) {
  font-family: "Sarabun", sans-serif;
  color: #bdbcbc;
  font-size: 12px;
}
.essv__outer__data div:nth-child(2) {
  font-family: "Sarabun", sans-serif;
  color: #bdbcbc;
}
.essv__outer__data div:nth-child(3) {
  font-family: "Sarabun", sans-serif;
  color: #0efd7a;
}

.employ__dynamic {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 50px 0;
}
.employ__dynamic__heading {
  font-size: 15px;
  font-style: bold;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  position: relative;
  background: transparent;
  z-index: 1;
}
.employ__dynamic__heading::before {
  content: "";
  position: absolute;
  top: 152%;
  left: 0;
  width: 150%;
  height: 4px;
  background-color: #0efd7a;
  z-index: -1;
}
.employ__dynamic__heading::after {
  content: "";
  position: absolute;
  top: -36%;
  left: 0;
  width: 150%;
  height: 35px;
  background: linear-gradient(to right, #fff, #a4e0d6);
  z-index: -1;
}
.employ__dynamic__value {
  display: flex;
  align-items: center;
}
.employ__dynamic__value div:nth-child(1) {
  font-family: "Sarabun", sans-serif;
  color: #0efd7a;
  margin-right: 8px;
}
.employ__dynamic__value div:nth-child(2) {
  font-size: 12px;
  font-family: "Sarabun", sans-serif;
  color: #bdbcbc;
}
.analysis {
  font-family: "Archivo", sans-serif;
  color: #000;
  font-size: 15px;
  margin: 40px 0;
}

