.employ__list__body__nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}
.employ__list__name {
}
.employ__list__name div:nth-child(1) {
  font-family: "Roboto", sans-serif;
}
.employ__list__name div:nth-child(2) {
