.MuiButton-root {
  padding: 6px 16px;
  font-size: 12px !important;
  min-width: 64px;
  box-sizing: border-box;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  font-weight: 500;
  line-height: 1.75;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  font-family: "Sarabun", sans-serif !important;
  color: #8f8c8c !important;
}
.MuiMenuItem-root {
  width: auto;
  overflow: hidden;
  font-size: 12px !important;
  box-sizing: border-box;
  min-height: 48px;
  font-weight: 400;
  line-height: 1.5;
  padding-top: 6px;
  white-space: nowrap;
  letter-spacing: 0.00938em;
  padding-bottom: 6px;
  font-family: "Sarabun", sans-serif !important;
  color: #8f8c8c !important;
}
.MuiLinearProgress-colorPrimary {
  background-color: #bdbcbc !important ;
}
.amount__paid {
  font-family: "Sarabun", sans-serif;
  color: #bdbcbc;
  margin: -5px 0;
}
.amount__paid__value {
  display: flex;
  align-items: center;
  margin: 3px 0;
}
.amount__paid__value div:nth-child(1) {
  margin-right: 5px;
  color: #0efd7a;
  font-family: "Sarabun", sans-serif;
  font-size: 18px;
}
.amount__paid__value div:nth-child(2) {
  font-family: "Sarabun", sans-serif;
  font-size: 15px;
  color: #bdbcbc;
}
.employ__list__progressbar {
}
.MuiLinearProgress-barColorPrimary {
  background-color: #0efd7a !important;
}
.MuiLinearProgress-colorPrimary {
  background-color: #bdbcbc;
}
